body {
    margin-right: auto;
    margin-left:  auto;
    max-width: 960px;
    padding-right: 5px;
    padding-left:  5px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

hr {
    border: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.prevoius-requests {
    text-align: center;
    margin-bottom: 2em;
}

.prevoius-requests__link {
    font-size: 1.1em;
    text-transform: uppercase;
}


.search-vin,
.vehicle-variables {
    text-align: center;
    margin-bottom: 2em;
}


.submit-form__input {
    display: inline-block;
    border: 1px solid #ccc;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 10px 10px;
    font-size: 1.1em;
    text-transform: uppercase;
}

.submit-form__button {
    border: 1px solid #ccc;
    border-left: 0px;
    padding: 10px 10px;
    font-size: 1.1em;
    text-transform: uppercase;
    cursor: pointer;
   
}
.submit-form__button:hover {
    background-color: #ccc;
}

.search-vin__table,
.vehicle-variables-list__table {
    border-spacing: 0;
    border-collapse: collapse;
    width: 100%;
    text-align: left;
}

.search-vin__table td,
.vehicle-variables-list__table td {
    width: 30%;
}
.search-vin__table td:nth-child(2n),
.vehicle-variables-list__table td:nth-child(2n) {
    width: 70%;
}

.search-vin__table tbody td,
.search-vin__table thead th,
.vehicle-variables-list__table tbody td,
.vehicle-variables-list__table thead th {
    padding: 6px 10px;
}

.search-vin__table thead th,
.vehicle-variables-list__table thead th {
    text-align: center;
}

.search-vin__table tbody tr,
.vehicle-variables-list__table tbody tr {
    background-color: #eee;
}

.search-vin__table tbody tr:nth-child(2n),
.vehicle-variables-list__table tbody tr:nth-child(2n) {
    background-color: transparent;
}

.search-vin__table tbody tr td,
.vehicle-variables-list__table tbody tr td {
    display: table-cell !important;
}

.vehicle-variable-item__resultItem {
    text-align: left;
}

@media screen and (max-width: 767px) {
    .submit-form__input, 
    .submit-form__button {
        font-size: 1em;
    }
    
    .search-vin__table td,
    .vehicle-variables-list__table td,
    .search-vin__table td:nth-child(2n),
    .vehicle-variables-list__table td:nth-child(2n) {
        width: 50%;
    }

        
    .search-vin__table tbody td,
    .search-vin__table thead th,
    .vehicle-variables-list__table tbody td,
    .vehicle-variables-list__table thead th {
        padding: 6px 5px;
    }
}
